//Funcin para cerrar sesin
function logout() {
    var respuesta = confirm("Ests seguro de cerrar tu sesin?");
    if (respuesta == true) {
        Swal.fire({
            position: 'center',
            icon: 'success',
            title: 'Sesin cerrada con xito',
            showConfirmButton: false,
            timer: 1500
        });
        return true;
    } else {
        Swal.fire({
            position: 'center',
            icon: 'info',
            title: 'No se ha cerrado la sesin',
            showConfirmButton: false,
            timer: 1500
        })
        return false;

    }
}

